<template>
    <div class="container">
        <div class="header">
            <div class="main clearfix">
                <div class="logo fl">
                    <img src="/static/img/logo.png" /></div>
                <top-nav></top-nav>
                <div class="header_person fr" v-if="isLoggedIn()">
                    <div class="header_personName" :title="userInfo().displayName" style="max-width: 170px;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;">
                        <img :src="userInfo().avatar" :title="userInfo().displayName" />
                        <span>{{userInfo().displayName}}</span></div>
                    <ul class="header_personList">
                        <li>
                            <a @click="goPage('/center/home')" href="javascript:void(0);">
                                <i class="fa fa-user-circle fa-lg" aria-hidden="true"></i></a>
                        </li>
                        <li>
                            <a @click="goPage('/center/person/setting')" href="javascript:void(0);">
                                <i class="fa fa-cog fa-lg" aria-hidden="true"></i></a>
                        </li>
                        <li class="header_personExit">
                            <a @click="goPage('/logout')" href="javascript:void(0);">
                                <i class="fa fa-sign-out fa-lg" aria-hidden="true"></i></a>
                        </li>
                    </ul>
                </div>
                <div class="header_login fr" v-else>
                    <a href="javascript:void()" @click="goLogin()"></a>
                    <a href="javascript:void()" @click="goReg()"></a></div>
            </div>
        </div>
        <!-- end of header -->
        <div class="contant">
            <div id="indexPanel">
                <router-view></router-view>
            </div>
            <div class="about">
                <div class="main clearfix">
                    <div class="about_tips clearfix fl">
                        <p></p>
                        <span>-
                            <br />Copyright  2016 xuanyuan.com.cn All Rights Reserved
                            <br /></span>
                        <p>
                        </p>
                        <div style="height: 30px;line-height: 30px;">
                            <a target="_blank" href="http://www.beian.gov.cn/portal/registerSystemInfo?recordcode=44010602003687">
                                <img src="http://www.beian.gov.cn/img/ghs.png" style="float:left;float: left;margin-top: 5px;" />
                                <p style="float:left;margin: 0px 0px 0px 5px;color: #737D88;font-size: 12px;">44010602003687</p></a>&nbsp;&nbsp;
                            <a target="_blank" href="http://www.miitbeian.gov.cn">
                                <p style="float:left;margin: 0px 0px 0px 5px;color: #737D88;font-size: 12px;">ICP16083248-8</p></a>
                        </div>
                    </div>
                    <ul class="about_bank clearfix fl">
                        <li>
                            <p></p>
                            <a href="http://xuanyuan.com.cn/" target="_blank"></a>
                            <a href="http://www.xuanyuan.com.cn/web/category/index/catid/17.html" target="_blank"></a></li>
                    </ul>
                    <ul class="about_qrcode clearfix fr">
                        <li>
                            <img src="/static/img/qrcode.jpg" />
                            <p></p>
                        </li>
                        <li>
                            <img src="/static/img/qrcode.jpg" />
                            <p></p>
                        </li>
                    </ul>
                </div>
            </div>
            <!-- end of about -->
            <div class="footerBG">2018</div>
            <!-- end of footer --></div>
    </div>
</template>

<script>
import TopNav from "@/components/shoppingMall/TopNav.vue";
import auth from "@/ssoAuth";
import http from "@/config/http";

export default {
  components: {
    TopNav
  },
  methods: {
    isLoggedIn: function() {
      return auth.loggedIn();
    },
    userInfo: function() {
      auth.checkTicket(auth.getTicket());
      return auth.userInfo();
    },
    goLogin: function() {
      return auth.goLogin();
    },
    goReg: function() {
      return auth.goReg();
    },
    goPage(path) {
      if (path === "/logout") {
         auth.logout();
         window.location.href = "http://" + window.location.host + "/#/";
      } else {
        window.location.href = "http://" + window.location.host + "/#" + path;
      }
    }
  },
  mounted() {
    let winHeight = null;
    if (window.innerHeight) winHeight = window.innerHeight;
    else if (document.body && document.body.clientHeight)
      winHeight = document.body.clientHeight;
    // window.document.getElementById("personCont").style.minHeight = winHeight;
    $("#indexPanel").css("min-height", winHeight - 60);
  }
};
</script>



