function merge(base, ext) {
	var temp = {};
	for (var key in base) {
		temp[key] = base[key];
	}
	for (var key in ext) {
		temp[key] = ext[key];
	}
	return temp;
}

// webpack buildprocess.env.NODE_ENVprocess.env
